  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  box-shadow: 0 0px 15px var(--gray-text-color);
  background-position: center;
  background-repeat: no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

input:checked + .slider {
  background-color: var(--orange-text-color);
  border: solid 2px var(--white-bg-color);
}

input:checked + .checkbox__light {
  color: var(--orange-text-color);
}

